import { sanityClient } from "@/lib/sanity";
import BlogClient from "./BlogClient";

/* -----------------------------
   METADATA (SAFE)
----------------------------- */
export async function generateMetadata(context) {
  const slug =
    context?.params && typeof context.params.slug === "string"
      ? context.params.slug
      : null;

  if (!slug) {
    return {
      title: "Blog | Cloud101",
      description: "Salesforce and Cloud learning articles by Cloud101."
    };
  }

  const post = await sanityClient.fetch(
    `*[_type=="post" && slug.current==$slug][0]{
      title,
      excerpt
    }`,
    { slug }
  );

  if (!post) {
    return {
      title: "Blog | Cloud101",
      description: "Salesforce and Cloud learning articles by Cloud101."
    };
  }

  return {
    title: `${post.title} | Cloud101`,
    description: post.excerpt?.slice(0, 155),
    alternates: {
      canonical: `https://cloud101.in/blog/${slug}`
    }
  };
}

/* -----------------------------
   PAGE WRAPPER (SAFE)
----------------------------- */
export default function Page(context) {
  const slug =
    context?.params && typeof context.params.slug === "string"
      ? context.params.slug
      : null;

  // CRITICAL: never render client without slug
  if (!slug) return null;

  return <BlogClient slug={slug} />;
}
